import {
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			userInfo:{},
			modalName: '',
			kefuList: [{
					name: '张三',
					phoneNubmer: '13227355241'
				},
				{
					name: '李四',
					phoneNubmer: '17798318776'
				}
			]
		};
	},
	mounted() {
		this.userInfo = uni.getStorageSync('userInfo');
	},
	methods: {
		...mapMutations(['logout']),
		toPages(url) {
			uni.navigateTo({
				url: url
			})
		},
		showModal(model) {
			this.modalName = model
		},
		hideModal() {
			this.modalName = null
		},
		callPhonenumber(phonenubmer) {
			console.log("电话：" + phonenubmer)
			uni.makePhoneCall({
				phoneNumber: phonenubmer
			});
		},
		//重启
		restart() {
			let url = '/pages/index/index'
			uni.reLaunch({
				url: url
			});
		},
		//推出程序
		exitProgram() {
			uni.showModal({
				title: '提示',
				content: '确定退出小程序？',
				success: function(res) {
					console.log("当前页面；", uni.getCurrentPages())
					if (res.confirm) {
						uni.navigateBack({
							delta:-1
						})
					}
				},
			})
		},
		
		exitLogin(){
			this.logout();
			uni.navigateTo({
				url:'/pages/user/login'
			})
		},
		
		// 版本更新
		checkUpdateVersion() {
			let that = this;
			const updateManager = uni.getUpdateManager();

			updateManager.onCheckForUpdate(function(res) {
				// 请求完新版本信息的回调
				console.log('新版本信息：', res.hasUpdate);
				if (!res.hasUpdate) {
					that.showModal('Modal')
				}
			});

			updateManager.onUpdateReady(function(res) {
				uni.showModal({
					title: '更新提示',
					content: '新版本已经准备好，是否重启应用？',
					success(res) {
						if (res.confirm) {
							// 新的版本已经下载好，调用 applyUpdate 应用新版本并重启
							updateManager.applyUpdate();
						}
					}
				});

			});

			updateManager.onUpdateFailed(function(res) {
				// 新的版本下载失败
			});
		},

	}
};
